    width: 80vw;
    margin: 10px auto;
  }
}

.todolist {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.todolist .todolist_list {
  list-style: none;
  padding: 0;
  width: 300px;
  max-height: 300px;
  overflow: auto;
}

.todolist .todolist_item {
  display: flex;
  transition: all 0.1s ease-out;
  user-select: none;
  border-bottom: 1px solid #999999;
}

.todolist .todolist_item:last-child {
  border-bottom: 0;
}

.todolist .todolist_item:nth-child(odd) {
  background: #e9fbfb;
}

.todolist .todolist_item:hover {
  background: #a8f0f0;
}

.todolist .todolist_item:active {
  transform: scale(0.95);
}

.todolist .todolist_item.checked {
  text-decoration: line-through;
  background: #d4f7d4;
}

.todolist .todolist_item.checked .todolist_item_icon {
  color: #0f570f;
}

.todolist .todolist_item_icon {
  padding: 10px;
}

.todolist .todolist_item_text {
  flex-grow: 1;
  padding: 10px;
  word-break: break-all;
}

.todolist .todolist_item_remove {
  opacity: 0.4;
  padding: 10px;
}

.todolist .todolist_item_remove:hover {
  color: #d92626;
  opacity: 1;
}

.todolist .todolist_controls {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.todolist input {
  transition: all 0.1s ease-out;
  border: 0;
  border-bottom: 1px solid #999999;
  outline: 0;
  text-align: center;
  padding: 10px;
}

.todolist input:focus {
  border-bottom: 2px solid #0077AA;
  text-align: left;
}

.hello {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
