#root {
	height: 100%;
	display: flex;
	flex-direction: column;
}

.maincontent {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin: $margin;
	padding: $padding;

	@include from(laptop) {
		width: 80vw;
		margin: $margin auto;
	}
}
